import React from 'react';
import { Card, CardHeader, CardTitle, CardContent } from "./ui/card";
import Button from "./ui/button";

interface TypePageProps {
  typeId: number;
  onSelectTask: (typeId: number, taskId: number) => void;
  onBack: () => void;
}

interface Task {
  id: number;
  name: string;
}

const TYPES: Record<number, string> = {
  1: "Binkan",
  2: "Sunseo",
  3: "Sabib",
  4: "Hamui",
  5: "Ubub",
  6: "Uhui",
  7: "Hureum",
  8: "Yoyakmoon",
};

const TASKS: Record<number, Task[]> = {
  1: [
    { id: 1, name: "Binkan - Sentence Selection" },
    { id: 2, name: "Binkan - Word or Phrase Selection" },
    { id: 3, name: "Binkan - Single Word Selection" },
    { id: 4, name: "Binkan - Phrase Selection" },
    { id: 5, name: "Binkan - Full_Score" },
  ],
  2: [
    { id: 1, name: "Sunseo - Sentence Selection" },
    { id: 2, name: "Sunseo - Full_Score" },
  ],
  3: [
    { id: 1, name: "Sabib - Sentence Selection" },
    { id: 2, name: "Sabib - Full_Score" },
  ],
  4: [
    { id: 1, name: "Hamui - Sentence Selection" },
    { id: 2, name: "Hamui - Phrase Selection" },
    { id: 3, name: "Hamui - Full_Score" },
  ],
  5: [
    { id: 1, name: "Ubub - Sentence Selection" },
    { id: 2, name: "Ubub - Word Selection" },
    { id: 3, name: "Ubub - Full_Score" },
  ],
  6: [
    { id: 1, name: "Uhui - Sentence Selection" },
    { id: 2, name: "Uhui - Word Selection" },
    { id: 3, name: "Uhui - Full_Score" },
  ],
  7: [
    { id: 1, name: "Hureum - Sentence Selection" },
    { id: 2, name: "Hureum - Unrelated Sentence Writing" },
    { id: 3, name: "Hureum - Full_Score" },
  ],
  8: [
    { id: 1, name: "Yoyakmoon - Summary Writing" },
    { id: 2, name: "Yoyakmoon - Word Selection" },
    { id: 3, name: "Yoyakmoon - Full_Score" },
  ],
};

const TypePage: React.FC<TypePageProps> = ({ typeId, onSelectTask, onBack }) => {
  const tasks = TASKS[typeId] || []; // typeId에 해당하는 태스크 가져오기
  const typeName = TYPES[typeId] || `Type ${typeId}`; // Type 이름 가져오기

  return (
    <div className="w-full max-w-6xl mx-auto p-6">
      <Card>
        <CardHeader className="flex flex-row justify-between items-center">
          <CardTitle>{typeName} Tasks</CardTitle>
          <Button
            variant="ghost"
            onClick={onBack}
          >
            Back to Types
          </Button>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-2 gap-4">
            {tasks.map((task) => (
              <Button
                key={task.id}
                variant="outline"
                className="h-24 p-4 flex flex-col justify-center items-center text-center hover:bg-gray-50"
                onClick={() => onSelectTask(typeId, task.id)}
              >
                <h2 className="text-xl font-semibold mb-2">{task.name}</h2>
                <p className="text-gray-600 text-center">View leaderboard and performance</p>
              </Button>
            ))}
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default TypePage;
