import React, { useMemo } from 'react';
import {
  Line,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  Scatter,
  ComposedChart,
  CartesianGrid,
  Legend
} from 'recharts';
import { WeeklyData } from '../types';

interface TaskPageProps {
  typeId: number;
  taskId: number;
  onBack: () => void;
  data: WeeklyData[] | null;
}

const TYPES = [
  { id: 1, name: "Binkan" },
  { id: 2, name: "Sunseo" },
  { id: 3, name: "Sabib" },
  { id: 4, name: "Hamui" },
  { id: 5, name: "Ubub" },
  { id: 6, name: "Uhui" },
  { id: 7, name: "Hureum" },
  { id: 8, name: "Yoyakmoon" },
];

const TASKS: Record<number, { id: number; name: string }[]> = {
  1: [
    { id: 1, name: "Sentence Selection" },
    { id: 2, name: "Word or Phrase Selection" },
    { id: 3, name: "Single Word Selection" },
    { id: 4, name: "Phrase Selection" },
    { id: 5, name: "Full_Score" },
  ],
  2: [{ id: 1, name: "Sentence Selection" }, { id: 2, name: "Full_Score" }],
  3: [{ id: 1, name: "Sentence Selection" }, { id: 2, name: "Full_Score" }],
  4: [
    { id: 1, name: "Sentence Selection" },
    { id: 2, name: "Phrase Selection" },
    { id: 3, name: "Full_Score" },
  ],
  5: [
    { id: 1, name: "Sentence Selection" },
    { id: 2, name: "Word Selection" },
    { id: 3, name: "Full_Score" },
  ],
  6: [
    { id: 1, name: "Sentence Selection" },
    { id: 2, name: "Word Selection" },
    { id: 3, name: "Full_Score" },
  ],
  7: [
    { id: 1, name: "Sentence Selection" },
    { id: 2, name: "Unrelated Sentence Writing" },
    { id: 3, name: "Full_Score" },
  ],
  8: [
    { id: 1, name: "Summary Writing" },
    { id: 2, name: "Word Selection" },
    { id: 3, name: "Full_Score" },
  ],
};

function transformDate(original: string) {
  // "2024-12-W4" → "2024-12-4" 식으로 변환
  const [year, month, week] = original.split('-');
  const weekNumber = week.replace('W', '');
  return `${year}-${month}-${weekNumber}`;
}

interface ChartDataPoint {
  xValue: number;           // 수치형 X축에서 사용할 값
  date: string;             // 주차 원본 정보
  performance: number;
  linePerformance: number | null;
  model_name: string;
}

const TaskPage: React.FC<TaskPageProps> = ({ typeId, taskId, onBack, data }) => {
  const combinedData = useMemo(() => {
    if (!data || data.length === 0) {
      return { points: [] as ChartDataPoint[], dateList: [] as string[] };
    }

    // 1) 날짜(주차)만 따로 뽑아서 정렬
    const rawDates = data.map((item) => transformDate(item.date));
    const uniqueDates = Array.from(new Set(rawDates)).sort();

    // 2) 주차(date) → 인덱스(0,1,2,...) 매핑
    const dateIndexMap: Record<string, number> = {};
    uniqueDates.forEach((d, i) => {
      dateIndexMap[d] = i;
    });

    // 3) 주차별로 데이터 포인트 생성
    const allPoints: ChartDataPoint[] = [];

    data.forEach((weekData) => {
      const dateStr = transformDate(weekData.date);
      const baseIndex = dateIndexMap[dateStr];

      // 이번 주차의 최고 성능 구하기
      const maxPerformance = weekData.attempts?.length
        ? Math.max(...weekData.attempts.map((a) => a.performance))
        : 0;

      weekData.attempts.forEach((attempt, attemptIdx) => {
        // 같은 주차 안에서 attemptIdx만큼 0.01 간격으로 이동
        const offset = attemptIdx * 0.01;
        const xVal = baseIndex + offset;

        allPoints.push({
          xValue: xVal,
          date: dateStr,
          performance: attempt.performance,
          linePerformance: attempt.performance === maxPerformance ? attempt.performance : null,
          model_name: attempt.model_name,
        });
      });
    });

    return { points: allPoints, dateList: uniqueDates };
  }, [data]);

  if (combinedData.points.length === 0) {
    return (
      <div className="flex justify-center items-center h-96">
        <p className="text-xl text-gray-500">
          No data available for type {typeId}, task {taskId}
        </p>
      </div>
    );
  }

  return (
    <div className="w-full max-w-6xl mx-auto p-8">
      <div className="bg-white rounded-lg shadow-lg">
        <div className="text-center border-b pb-6 p-6">
          <div className="flex justify-between items-center">
            <h2 className="text-2xl font-bold">
              {TYPES.find((type) => type.id === typeId)?.name} -{" "}
              {TASKS[typeId]?.find((task) => task.id === taskId)?.name} Performance
            </h2>
            <button 
              onClick={onBack}
              className="px-6 py-2 border rounded-md hover:bg-gray-100 transition-colors"
            >
              Back to Tasks
            </button>
          </div>
        </div>
        <div className="p-8">
          <div className="h-96 w-full border rounded-lg">
            <ResponsiveContainer width="100%" height="100%">
              <ComposedChart
                data={combinedData.points}
                margin={{ top: 20, right: 30, left: 20, bottom: 20 }}
              >
                <CartesianGrid strokeDasharray="3 3" />

                {/*
                  1) XAxis를 수치형(type="number")으로 설정
                  2) domain을 [0, 최대 인덱스]로 지정
                  3) tickFormatter에서 dateList[idx]를 다시 W주차 형태로 바꿔서 표기
                */}
                <XAxis
                  dataKey="xValue"
                  type="number"
                  domain={[0, combinedData.dateList.length - 1]}
                  tickCount={combinedData.dateList.length}
                  tickFormatter={(val: number) => {
                    // 예: val = 0, 1, 2, ... 에서 정수 부분만 골라 dateList[idx]를 가져옴
                    const idx = Math.floor(val);
                    if (idx >= 0 && idx < combinedData.dateList.length) {
                      return combinedData.dateList[idx].replace(/-(\d+)$/, '-W$1');
                    }
                    return '';
                  }}
                />

                <YAxis domain={[0, 100]} ticks={[0, 20, 40, 60, 80, 100]} />

                <Tooltip
                  content={({ active, payload }) => {
                    if (active && payload && payload.length) {
                      const data = payload[0].payload;
                      return (
                        <div className="bg-white p-4 border rounded shadow">
                          <p className="text-sm text-gray-600">
                            Week: {data.date.replace(/-(\d+)$/, '-W$1')}
                          </p>
                          <p className="text-sm font-medium">
                            Model: {data.model_name}
                          </p>
                          <p className="text-sm font-medium">
                            Performance: {Number(data.performance).toFixed(2)}%
                          </p>
                          {data.linePerformance && (
                            <p className="text-sm text-blue-600">
                              (Best Performance)
                            </p>
                          )}
                        </div>
                      );
                    }
                    return null;
                  }}
                />

                <Legend />

                <Scatter
                  name="Model Attempts"
                  dataKey="performance"
                  fill="#94a3b8"
                  isAnimationActive={false}
                  shape={(props: any) => {
                    const { cx, cy } = props;
                    return (
                      <circle
                        cx={cx}
                        cy={cy}
                        r={3}
                        fill="#94a3b8"
                        stroke="#475569"
                        strokeWidth={1}
                      />
                    );
                  }}
                  activeShape={(props: any) => {
                    const { cx, cy } = props;
                    return (
                      <circle
                        cx={cx}
                        cy={cy}
                        r={5}
                        fill="#94a3b8"
                        stroke="#475569"
                        strokeWidth={2}
                      />
                    );
                  }}
                />

                <Line
                  name="Best Performance"
                  dataKey="linePerformance"
                  type="monotone"
                  stroke="#2563eb"
                  strokeWidth={2}
                  dot={{ r: 5, fill: "#2563eb" }}
                  connectNulls
                />
              </ComposedChart>
            </ResponsiveContainer>
          </div>
        </div>
      </div>
    </div>
  );
};

export default TaskPage;
