import React, { useState, useEffect } from 'react';
import MainPage from './components/MainPage';
import TypePage from './components/TypePage';
import TaskPage from './components/TaskPage';
import { StructuredData, PerformanceData } from './types';
import Papa from 'papaparse';
import { csvData } from './data/csvData';

const App: React.FC = () => {
  const [currentPage, setCurrentPage] = useState<'main' | 'type' | 'task'>('main');
  const [selectedType, setSelectedType] = useState<number | null>(null);
  const [selectedTask, setSelectedTask] = useState<number | null>(null);
  const [data, setData] = useState<StructuredData | null>(null);

  useEffect(() => {
    const loadData = async () => {
      try {
        console.log('🚀 Starting to load CSV file...');
        
        // CSV 파싱
        Papa.parse<PerformanceData>(csvData, {
          header: true,
          dynamicTyping: true,
          skipEmptyLines: true,
          complete: (results) => {
            console.log('📊 Parsed CSV data:', results.data.slice(0, 3), `(Total rows: ${results.data.length})`);
            
            // 데이터 구조화
            const structuredData: StructuredData = {};
            
            results.data.forEach(row => {
              if (!row.type_id || !row.task_id) {
                console.warn('⚠️ Invalid row:', row);
                return;
              }

              if (!structuredData[row.type_id]) {
                structuredData[row.type_id] = {};
                console.log(`📁 Created new type_id: ${row.type_id}`);
              }
              if (!structuredData[row.type_id][row.task_id]) {
                structuredData[row.type_id][row.task_id] = [];
                console.log(`📂 Created new task_id: ${row.task_id} for type_id: ${row.type_id}`);
              }

              // 날짜별로 데이터 그룹화
              let dateEntry = structuredData[row.type_id][row.task_id].find(
                d => d.date === row.date
              );

              if (!dateEntry) {
                dateEntry = {
                  date: row.date,
                  attempts: []
                };
                structuredData[row.type_id][row.task_id].push(dateEntry);
                console.log(`📅 Created new date entry: ${row.date} for type_id: ${row.type_id}, task_id: ${row.task_id}`);
              }

              dateEntry.attempts.push({
                performance: row.performance,
                model_name: row.model_name,
                type_id: row.type_id,
                task_id: row.task_id
              });
            });

            console.log('✅ Final structured data:', structuredData);
            setData(structuredData);
          },
          error: (error: any) => {
            console.error('❌ Error parsing CSV:', error);
          }
        });
      } catch (error) {
        console.error('❌ Error processing data:', error);
      }
    };

    loadData();
  }, []);

  // Type 선택 핸들러
  const handleSelectType = (typeId: number) => {
    console.log('👆 Selected Type:', typeId);
    setSelectedType(typeId);
    setCurrentPage('type');
  };

  // Task 선택 핸들러
  const handleSelectTask = (typeId: number, taskId: number) => {
    console.log('👆 Selected Task:', taskId, 'for Type:', typeId);
    setSelectedType(typeId);
    setSelectedTask(taskId);
    setCurrentPage('task');
  };

  // 뒤로가기 핸들러
  const handleBack = () => {
    if (currentPage === 'task') {
      console.log('🔙 Going back to Type page');
      setCurrentPage('type');
      setSelectedTask(null);
    } else {
      console.log('🔙 Going back to Main page');
      setCurrentPage('main');
      setSelectedType(null);
    }
  };

  // Task 데이터 가져오기
  const getTaskData = (typeId: number, taskId: number) => {
    if (!data || !data[typeId] || !data[typeId][taskId]) {
      console.warn('⚠️ No data found for:', { typeId, taskId });
      return null;
    }
    console.log('📊 Retrieved data for:', { typeId, taskId }, data[typeId][taskId]);
    return data[typeId][taskId];
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {currentPage === 'main' ? (
        <MainPage onSelectType={handleSelectType} />
      ) : currentPage === 'type' && selectedType ? (
        <TypePage 
          typeId={selectedType} 
          onSelectTask={handleSelectTask}
          onBack={handleBack}
        />
      ) : selectedType && selectedTask ? (
        <TaskPage 
          typeId={selectedType}
          taskId={selectedTask}
          onBack={handleBack}
          data={getTaskData(selectedType, selectedTask)}
        />
      ) : null}
    </div>
  );
};

export default App;